import { useEffect, useState } from "react";

function Equip(props){
    
    const [mode,setMode]=useState(false)
    const Equips=[
        {key:1, value:' '},
        {key:2, value:' '},
        {key:3, value:''}
    ]

    let select=null;
    
    useEffect(()=>{
        if (props.purpose===''){
            setMode(true)
            props.setPurpose('')
        }
    })

    if (mode){
        select= <input type='text' onChange={(e)=> props.setPurpose(e.currentTarget.value)} value={props.purpose} name='purpose' placeholder=''></input>;
    }

    return(
        <div>
        <p> </p>
            {(select === null)?
            <select onChange={props.onChangeHandler} value={props.purpose}>
            {Equips.map((item)=>(<option key ={item.key} name='purpose' value={item.value}>
            {item.value}
            </option>))}
            </select>
            : select
            }
        </div>
    )
}

export default Equip;