import { useState } from 'react';
import {Link} from 'react-router-dom';
import './Modal.css';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

function Modalalert(props){
    return (
        <div className='Modal' onClick={props.closeModal}>
        <div className='modalBody' onClick={(e)=> e.stopPropagation()}>
        <Button id='modalClose' onClick={props.closeModal} variant="secondary">
                 
                </Button>
                <p>
                <p>
                 : {props.name}
                </p><p>
                 : {props.studentid}
                </p><p>
                 : {props.phone}
                </p><p>
                 : {props.major}
                </p>
                { 
                    (props.detail==='')?
                    null
                    :
                    <p>
                     : {props.detail}
                    </p>
                }

                {
                    typeof (props.purpose) === 'undefined' ? 
                    null
                    : <p>
                      : {props.purpose}
                    </p>
                }

                {
                    typeof (props.printer) === 'undefined' ? 
                    null
                    : <p>
                      : 3D  {props.printer}
                    </p>
                }

                {
                    typeof (props.leader) === 'undefined' ? 
                    null
                    : <p>
                      : {props.leader}
                    </p>
                }

                {
                    typeof (props.tool) === 'undefined' ? 
                    null
                    : (typeof (props.inputNum) === 'undefined'  ? 
                        <p>   : {props.tool}</p>
                        : <p>   : {props.tool} {props.inputNum}</p>
                        )
                }
                {
                    typeof (props.time) === 'undefined' ? 
                    null
                    : <p>
                      : {props.time}
                    </p>
                }
                
                <Link to='/survey'>
                <Button onClick={props.closeModal} variant="success"></Button></Link>
                </p>
        </div></div>
    )
}

export default Modalalert;