import Reset from "./Reset";
import Major from "./components/Major";
import PhoneNum from "./components/PhoneNum";
import Id from "./components/Id";
import Modalalert from "./components/Modal";
import Name from "./components/Name";

import { useState } from "react";
import {addDoc, collection, doc, getDocs} from 'firebase/firestore';
import {db} from './Firebase';

function Table(props){
    const [datetime, setDatetime]=useState('')
    const [name, setName]= useState('')
    const [studentid, setStudentid]= useState('')
    const [phone, setPhone]= useState('')
    const [major, setMajor]=useState('');  
    const [details, setDetails]=useState(''); 
    const [leader, setLeader]=useState(''); 
    const [time, setTime]= useState('30')

    //
    const onChangeHandler=(event)=>{
        setMajor(event.currentTarget.value);
    }

    const onChangeHandler2=(event)=>{
        setDetails(event.currentTarget.value);
    }

    //
    const onChangeHandler3=(e)=>{
        const regex=/^[-|-]{0,5}$/;
        const regex2=/^[a-zA-Z\s]{0,20}$/;
        if (regex.test(e.target.value)){
            setLeader(e.target.value);
        }
        else if (regex2.test(e.target.value)){
            setLeader(e.target.value);
        } 
    }

    //
    const handlePress=(e)=>{
        const regex=/^[0-9\b -]{0,13}$/;
        if (regex.test(e.target.value)){
            setPhone(e.target.value);
        };
    }

    //
    const handlePress2=(e)=>{
        const regex=/^[0-9\b ]{0,10}$/;
        if (regex.test(e.target.value)){
            setStudentid(e.target.value);
        }
    }
    
    //
    const handlePress3=(e)=>{
        const regex=/^[-|-]{0,5}$/;
        const regex2=/^[a-zA-Z\s]{0,20}$/;
        if (regex.test(e.target.value)){
            setName(e.target.value);
        }
        else if (regex2.test(e.target.value)){
            setName(e.target.value);
        }    
    }

    const [modal, setModal]=useState(false);

    //users collection 
    const usersCollectionRef=collection(db,'table');

    //
    const createUser=async()=>{
        await addDoc(usersCollectionRef, {name: name, studentid:studentid, phone:phone, detail:details, major:major, leader:leader, datetime:datetime, purpose:' ', state:true, time:time});
    };

    const [alert1,Setalert1]=useState('')
    const [alert2,Setalert2]=useState('')
    const [alert3,Setalert3]=useState('')
    const [alert4,Setalert4]=useState('')

    const setmodal=()=>{
        const date=new Date()
        setModal(true);
        setDatetime(date)
        if (name.length ===0){
            setModal(false);
            Setalert1(' ');

        } else {
            Setalert1('');
        }
        
        const regExp = /^01(?:0|1|)-\d{3,4}-\d{4}$/;

        if (phone.length !==13){
            setModal(false);
            Setalert2('  ');
        } else if (!regExp.test(phone)){
            setModal(false);
            Setalert2('010   ');
        }
        else {
            Setalert2('');
        }

        const regExp2 = /^20(?:0|1|2)\d{7}$/
        if (studentid.length !==10){
            setModal(false);
            Setalert3('(10)  ');
        } else if (!regExp2.test(studentid)){
            setModal(false);
            Setalert3('  ');
        } else {
            Setalert3('');
        }
        if (leader===''){
            setModal(false);
            Setalert4('(1  )  ');
        }
        else {
            Setalert4('');
        }
    };

    const offmodal=()=>{
        setModal(false);
    };

    return (
        <div style={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            height: '100vh',
          }}>
        <article>
        <p><Reset/></p>
        <h2>   </h2>   
        <form onSubmit={event=>{
            event.preventDefault();
        }}> 
            <Name name={name} handlePress={handlePress3} setName={setName}/>
            <small>{alert1}</small>
            <PhoneNum phone={phone} handlePress={handlePress} setPhone={setPhone}/>
            <small>{alert2}</small>
            <Id studentid={studentid} handlePress={handlePress2}/>
            <small>{alert3}</small>
            <Major major={major} setDetails={setDetails} onChangeHandler={onChangeHandler} onChangeHandler2={onChangeHandler2} />
            
            <p>
              (   1,    )</p>
            <small>        </small>
            <p>
            <input type='text'  name='leader' placeholder=' '
            onChange={onChangeHandler3} value={leader}></input></p>
            <small>{alert4}</small>
            <p> </p>
            <select onChange={(events)=>{
            setTime(events.target.value);}}>  
            <option name='time' value= '30' >30</option>
            <option name='time' value= '1' >1</option>
            <option name='time' value='1 30' >1 30</option>
            <option name='time' value='2' >2</option>
            <option name='time' value='2 30' >2 30</option>
            <option name='time' value='3' >3</option>
            <option name='time' value='3 30' >3 30</option>
            <option name='time' value='4' >4</option>
            <option name='time' value='4 30' >4 30</option>
            <option name='time' value='5' >5</option>
            <option name='time' value='5 30' >5 30</option>
            <option name='time' value='6' >6</option>
            <option name='time' value='6 30' >6 30</option>
            <option name='time' value='7' >7</option>
            </select>
        </form>
        <input onClick={()=>
        setmodal()} type='submit'></input>
        </article>
        {
            modal === true ? 
            <Modalalert closeModal={()=> {createUser(); offmodal() }} name={name} phone={phone} studentid={studentid} major={major} detail={details} leader={leader} time={time}/>
            : null
        }
        </div>
    )
  
}

export default Table;
  
