import { useEffect, useState } from "react";

function Tool(props){

    const [mode,setMode]=useState(false)
    const Tools=[
        {key:1, value:'usb-1'},
        {key:2, value:'usb-2'},
        {key:3, value:'usb-3'},
        {key:4, value:'usb-4'},
        {key:5, value:'usb-5'},
        {key:6, value:''},
        {key:7, value:''},
        {key:8, value:''},
        {key:9, value:''},
        {key:10, value:''},
        {key:11, value:''},
        {key:12, value:''},
        {key:13, value:''}
    ]

    let select=null;
    useEffect(()=>{
        if (props.tool===''){
            setMode(true)
            props.setTool('')
        }
    })

    if (mode){
        select= <input type='text' onChange={(e)=> props.setTool(e.currentTarget.value)} value={props.tool} name='tool' placeholder=''></input>;
    }

    // let selectnum=null;
    // if (props.tool ==='usb'){
    //     selectnum= <input type='text' onChange={props.onChangeInfo} value={props.inputNum} name='number' placeholder=''></input>;
    // }

    return(
        <div>
        <p> </p><small>   !</small>
        <p>
            {(select === null)?
            <select onChange={props.onChangeHandler} value={props.tool}>
            {Tools.map((item)=>(<option key ={item.key} name='tool' value={item.value}>
            {item.value}
            </option>))}
            </select>
            : select
            }
            {/* {selectnum} */}
        </p>
        </div>
    )
}

export default Tool;